import React from "react";
import { useState } from "react";
import "../styles/IDE.scss";
import Editor from "@monaco-editor/react";
import {
  faBriefcase,
  faBug,
  faCodeBranch,
  faKeyboard,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Terminal } from "./Terminal";
import ResizePanel from "react-resize-panel";

function IDE() {
  let examples = {
    javascript: 'console.log("hello javascript")',
    c: '#include <stdio.h>\nint main(int argc, char* argv[])\n{\n    printf("Hello World");\n    return 0;\n}\n',
    python: 'print("hello python")',
  };
  let [sidebarBtn, setSidebarBtn] = useState("");
  let [editorReady, setEditerReady] = useState(false);
  let [user, setUser] = useState("");
  let [file, setFile] = useState(false);
  function handleEditorDidMount() {
    setEditerReady(true);
  }
  const clickHandler = (e) => {
    setSidebarBtn(e.currentTarget.value);
  };

  return (
    <div>
      {/*--------------navbar--------------*/}
      <div className="container"></div>
      <div className="nav-bar">
        <div className="first-nav">
          <p>Together Coding</p>
          <a></a>
          <a></a>
          <a></a>
          <a></a>
          <a></a>
          <a>Git</a>
          <a></a>
          <a></a>
          <a></a>
          <span className="user">{user}</span>
        </div>
        <div className="second-nav">
          <span>workspace / test / src / main. c</span>{" "}
          <div className="second-toolbar">
            <button></button>
            <button>  </button>
            <button></button>
            <div className="toolbar-divider"> </div>
            <button>more.....</button>
          </div>
        </div>
      </div>
      {/*-------------side bar--------------*/}
      <div className="main">
        <div className="side-bar">
          <div className="side-btn">
            <button value="" onClick={clickHandler}>
              <FontAwesomeIcon icon={faBriefcase} />
            </button>
            <span></span>
            <button value="" onClick={clickHandler}>
              <FontAwesomeIcon icon={faKeyboard} />
            </button>
            <span></span>

            <button onClick={clickHandler} value="git">
              <FontAwesomeIcon icon={faCodeBranch} />
            </button>
            <span>git</span>

            <button onClick={clickHandler} value="">
              <FontAwesomeIcon icon={faBug} />
            </button>
            <span></span>
          </div>
        </div>
        {sidebarBtn === "" ? (
          <SideExplorer setFile={setFile} file={file} />
        ) : null}
        {sidebarBtn === "" ? (
          <SideExplorer2 setFile={setFile} file={file} />
        ) : null}
        {sidebarBtn === "git" ? (
          <SideExplorer3 setFile={setFile} file={file} />
        ) : null}
        {sidebarBtn === "" ? (
          <SideExplorer4 setFile={setFile} file={file} />
        ) : null}
        {/*-----------code input and terminal-----------*/}
        <div className="terminal">
            <div className="editor">
              <Editor
                height="70vh"
                theme="vs-dark"
                defaultLanguage="c"
                defaultValue={examples["c"]}
                editorDidMount={handleEditorDidMount}
              />
            </div>
          <Terminal />
        </div>
      </div>
    </div>
  );
}
function SideExplorer(props) {
  return (
    <div className="side-explorer">
      <p className="side-navbar">
        <span></span>
      </p>
      <div
        className="files"
        onClick={() => {
          props.setFile(!props.file);
        }}
      >
        test2
      </div>
      {props.file ? <div className="files"> &gt; </div> : null}
    </div>
  );
}
function SideExplorer2(props) {
  return (
    <div className="side-explorer">
      <p className="side-navbar">
        <span></span>
      </p>
      <div
        className="files"
        onClick={() => {
          props.setFile(!props.file);
        }}
      >
        test2
      </div>
      {props.file ? <div className="files"> &gt; </div> : null}
    </div>
  );
}
function SideExplorer3(props) {
  return (
    <div className="side-explorer">
      <p className="side-navbar">
        <span>git</span>
      </p>
      <div style={{ color: "#b9c3dd" }}>  </div>
    </div>
  );
}
function SideExplorer4(props) {
  return (
    <div className="side-explorer">
      <p className="side-navbar">
        <span></span>
      </p>
      <div
        className="files"
        onClick={() => {
          props.setFile(!props.file);
        }}
      >
        
      </div>
      {props.file ? <div className="files"> &gt; </div> : null}
    </div>
  );
}

export default IDE;
