import React, { useState, useEffect } from "react";
import Modal from "react-modal";
import { Link } from "react-router-dom";
import "../styles/myInfo.scss";
import axios from "axios";
import { API_URL } from "../constants.js";
import { useHistory } from "react-router-dom";
let mockData = {
  id: 1,
  name: "",
  classAsStudent: ["", "2"],
  classAsTeacher: ["", " ", ""],
};

function MyInfo() {
  useEffect(() => {
    let body = {
      token: localStorage.getItem("access_token"),
    };
    axios.post(`${API_URL}/auth/token`, body).then((res) => {
      console.log(res);
      setEmail(res.data.email);
    });
  }, []);
  //**********  ()
  const [userEmail, setEmail] = useState("");
  //***********************
  let history = useHistory();
  const [modalIsOpen, setModalIsOpen] = useState(false);

  const [courseName, setCourseName] = useState("");
  const [coursePwd, setCoursePwd] = useState("");
  const [teacherEmail, setTeacherEmail] = useState("");
  const [courseDescription, setCourseDescription] = useState("");
  const [participants, setParticipants] = useState([]);

  let [participantsInput, setParticipantsInput] = useState("");
  const courseNameEvent = (e) => {
    setCourseName(e.target.value);
  };
  const coursePwdEvent = (e) => {
    setCoursePwd(e.target.value);
  };
  const teacherEmailEvent = (e) => {
    setTeacherEmail(e.target.value);
  };
  const courseDesEvent = (e) => {
    setCourseDescription(e.target.value);
  };

  const submitEvent = (e) => {
    e.preventDefault();

    let body = {
      name: courseName,
      password: coursePwd,
      teacherName: teacherEmail,
      description: courseDescription,
      participant: participants,
    };
    axios.post(`${API_URL}/api/course`, body).then((res) => {
      console.log(res);
    });
  };

  const inputPlus = () => {
    if (participantsInput === "") {
      alert("  !");
      return false;
    }
    if (window.confirm(participantsInput + " ?")) {
      let participantsCopy = [...participants];
      participantsCopy.push(participantsInput);
      setParticipants(participantsCopy);
      setParticipantsInput("");
    } else {
      return false;
    }
  };
  console.log(participants);
  const logOutCtrl = () => {
    localStorage.removeItem("access_token");
    history.push("/");
  };

  return (
    <div>
      <div className="Info-nav-bar">
        {userEmail}   <button onClick={logOutCtrl}></button>
      </div>
      <button onClick={() => setModalIsOpen(true)}> </button>
      <div className="main-box">
        <div className="teacher">
          <p>  </p>
          {mockData.classAsTeacher.map((x, idx) => {
            return (
              <span className="class">
                <Link
                  to={{
                    pathname: "/teacher-main/" + x,
                    state: {
                      class: x,
                    },
                  }}
                >
                  {x}
                </Link>
              </span>
            );
          })}
        </div>
        <div className="student">
          <p>  </p>
          {mockData.classAsStudent.map((x, idx) => {
            return (
              <span className="class">
                <Link
                  to={{
                    pathname: "/student-main/" + x,
                    state: {
                      class: x,
                    },
                  }}
                >
                  {x}
                </Link>
              </span>
            );
          })}
        </div>
      </div>
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={() => setModalIsOpen(false)}
        style={{
          overlay: {
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgba(15, 15, 15, 0.79)",
          },
          content: {
            position: "absolute",
            top: "60px",
            left: "35%",
            width: "30%",
            height: "80%",
            border: "1px solid #ccc",
            background: "#fff",
            overflow: "auto",
            WebkitOverflowScrolling: "touch",
            borderRadius: "4px",
            outline: "none",
            padding: "20px",
          },
        }}
      >
        <div className="modal-nav">
          <button onClick={() => setModalIsOpen(false)}>X</button>
        </div>
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <form
            onSubmit={submitEvent}
            style={{ display: "flex", flexDirection: "column" }}
          >
            <label> </label>
            <input value={courseName} onChange={courseNameEvent}></input>
            <label> </label>
            <input
              type="password"
              value={coursePwd}
              onChange={coursePwdEvent}
            />
            <label> </label>
            <input
              type="email"
              value={teacherEmail}
              onChange={teacherEmailEvent}
            />
            <label> </label>
            <input value={courseDescription} onChange={courseDesEvent} />
            <label>
                <button onClick={inputPlus}></button>
            </label>
            <input
              value={participantsInput}
              onChange={(e) => {
                setParticipantsInput(e.target.value);
              }}
            />
            <button> </button>
          </form>
        </div>
      </Modal>
    </div>
  );
}

export default MyInfo;
