import axios from "axios";
import { useRef, useEffect, useState } from "react";
import { SSHClient } from "../utils/websocket";
import { XTerm } from "xterm-for-react";
import { FitAddon } from "xterm-addon-fit";
import "xterm/css/xterm.css";
import {API_URL, RUNTIME_BRIDGE_URL} from "../constants";



/**
 *  
 * TODO:  HTML   width, height     .
 * @return {JSX.Element}
 * @constructor
 */
export function TerminalMenu() {
  let token = localStorage.getItem("access_token");

  // xterm Terminal
  let xtermRef = useRef();
  let terminalWrapper = useRef();
  // FitAddon: xtermRef      
  const fitAddon = new FitAddon();
  // Terminal options
  let [ptyCols, setPtyCols] = useState(0);
  let [ptyRows, setPtyRows] = useState(0);
  const xtermOptions = {
    cursorBlink: true,
    theme: {
      background: "#2f3336",
      foreground: "#ffffff",
    },
  };

  let sshClient = useRef();
  useEffect(() => {
    initTerminal();
  }, []);

  let initTerminal= () => {
    // xterm 
    terminalWrapper.current = document.getElementsByClassName('terminal-wrapper')[0];
    window.onresize = resizeTerminal;
    fitAddon.fit();
  }


  /**
   * SSH   ,  
   * @param text
   * @return {*}
   */
  let encodeText = (text) => {
    return text;
  };

  /**
   *
   * @param text    
   * @return {string}
   */
  let decodeText = (text) => {
    // Promise       
    return String.fromCharCode.apply(null, new Uint8Array(text));
  };

  /**
   *  xterm   ,  SSH relay  .
   * @param data
   */
  let onData = (data) => {
    sshClient.current.emit("SSH", encodeText(data));
  };

  let onScroll = (newPos) => {
    // TODO
    console.log("onScroll", newPos);
  };

  /**
   * window.onresize 
   */
  let resizeTerminal = () => {
    //       
    fitAddon.fit();

    //  pty  
    let cols = parseInt(
      terminalWrapper.current.clientWidth /
        xtermRef.current.terminal._core._renderService._renderer.dimensions
          .actualCellWidth
    );
    let rows = parseInt(
      terminalWrapper.current.clientHeight /
        xtermRef.current.terminal._core._renderService._renderer.dimensions
          .actualCellHeight
    );

    if (ptyCols === cols && ptyRows === rows) return;
    onResize({ cols, rows });
    setPtyCols(cols);
    setPtyRows(rows);
  };

  /**
   * pty  
   * @param cols pty width
   * @param rows pty height
   */
  let onResize = ({ cols, rows }) => {
    xtermRef.current.terminal.resize(cols, rows);
    if (sshClient.current) {
      sshClient.current.emit("SSH_RESIZE", { cols, rows });
    }
  };

  /**
   * SSH relay  
   *        .
   */
  async function initRuntime() {
    let contInfo = {}

    // FIXME headers, payload     
    let headers = {Authorization: "Bearer " + localStorage.getItem("access_token") || ""}
    let payload = {
      name: "C (gcc11)",
      lang: "C",
    }
    let res = await axios.post(`${RUNTIME_BRIDGE_URL}/api/containers/launch`, payload, {headers})
    contInfo.url = res.data.url;
    contInfo.port = res.data.port;

    if (!contInfo.url || !contInfo.port) {
      return;
    }
    sshClient.current = new SSHClient(`${contInfo.url}:${contInfo.port}`, token);

    //       .
    sshClient.current.on("AUTHENTICATE", (data) => {
      if (data === "AUTHENTICATE") {
        sshClient.current.emit("SSH_CONNECT", "SSH_CONNECT");
      }
    });

    // 'SSH'     ,   .
    sshClient.current.on("SSH_RELAY", (data) => {
      xtermRef.current.terminal.write(decodeText(data));
    });
  }


  return (
    <>
      <button onClick={initRuntime}>
        []  
      </button>
      <XTerm
        ref={xtermRef}
        className={"xterm-terminal"}
        options={xtermOptions}
        addons={[fitAddon]}
        onData={onData}
        onResize={onResize}
        onScroll={onScroll}
      />
    </>
  );
}
