import React, { useState } from "react";
import axios from "axios";
import { API_URL } from "../constants";
import { useHistory } from "react-router-dom";

// POST /auth/signup
function RegisterPage() {
  let history = useHistory();
  let [email, setEmail] = useState("");
  let [pwd, setPwd] = useState("");
  let [checkPwd, setCheckPwd] = useState("");
  let [name, setName] = useState("");

  let [pwdLength, setPwdLength] = useState(false);
  const emailEvent = (e) => {
    setEmail(e.target.value);
  };
  const pwdEvent = (e) => {
    setPwd(e.target.value);
    if (e.target.value.length >= 8) {
      setPwdLength(true);
    } else {
      setPwdLength(false);
    }
  };
  const checkPwdEvent = (e) => {
    setCheckPwd(e.target.value);
  };
  const nameEvent = (e) => {
    setName(e.target.value);
  };
  const submitEvent = (e) => {
    e.preventDefault();
    if (pwd !== checkPwd) {
      return alert("  .");
    }
    if (email === "") {
      return alert("  ");
    }
    if (pwd === "") {
      return alert("  ");
    }
    if (pwd === "") {
      return alert("   ");
    }
    if (name === "") {
      return alert(" ");
    }

    let body = {
      email: email,
      password: pwd,
      name: name,
    };
    axios
      .post(`${API_URL}/auth/user`, body)
      .then((res) => {
        if (res.status === 200) {
          alert(" !");
          history.push("/");
        }
      })
      .catch((error) => console.log(error));
  };
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <form
        onSubmit={submitEvent}
        style={{ display: "flex", flexDirection: "column" }}
      >
        <label></label>
        <input type="email" value={email} onChange={emailEvent}></input>
        <label> (, 8~20)</label>
        <span>
          {pwdLength ? null : (
            <span style={{ color: "red", fontSize: "10px" }}>
              8  
            </span>
          )}
        </span>
        <input type="password" value={pwd} onChange={pwdEvent} />
        <label> </label>
        <input type="password" value={checkPwd} onChange={checkPwdEvent} />
        <label></label>
        <input value={name} onChange={nameEvent} />
        <button></button>
      </form>
    </div>
  );
}

export default RegisterPage;
